// src/components/CaptureButton.js
import React from "react";

const MyCaptureButton = ({ isScreenSharing, startScreenCapture }) => {
  return (
    <div>
      <button onClick={startScreenCapture} disabled={isScreenSharing}>
        {isScreenSharing ? " " : " "}
      </button>
    </div>
  );
};

export default MyCaptureButton;
